import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { routerTransition } from '../router.animations';
import { ApiService } from '../shared/services/api.service';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss'],
    animations: [routerTransition()]
})
export class LoginComponent implements OnInit {
    constructor(public router: Router, public apiService: ApiService, private formBuilder: FormBuilder) {
        console.log('constructor');
        this.loginForm = this.formBuilder.group({
            email: new FormControl('', [Validators.required, Validators.email])
        });

        console.log('loginForm', this.loginForm);
    }

    email: String;
    userNotFound: String = '';
    loginForm: FormGroup;

    ngOnInit() {}

    onLoggedin() {
        console.log('onLoggedin');
        let isFound: Boolean = false;

        this.apiService.users().forEach((e) => {
            if (e.email === this.email) {
                isFound = true;
                this.userNotFound = '';
                localStorage.setItem('loggedUser', JSON.stringify(e));
                this.router.navigate(['/inventory']);
            }
        });
        if (!isFound) {
            this.userNotFound = 'User Not Found';
        } else {
            this.userNotFound = '';
        }
        //
    }
}
